import React, { useRef } from "react";
import { useParams } from "react-router-dom";

import useTypedSelector from "../../../hooks/useTypedSelector";
import useFetch from "../../../hooks/useFetch";

import createlUrlFetch from "../../../misc/createUrlFetch";

import Card from "../../shared/Cards/Card";

const Details: React.FC = () => {
  const { id } = useParams<{ id: string }>();

  const url = createlUrlFetch(id);

  const isSingleRequest = useRef(true);

  const obj = useTypedSelector(state => {
    // console.log(state);
    return state.fetch;
  });

  const { response, error, isLoading, isResponded } = obj;

  const request = useTypedSelector(state => state.request);

  request.isSubmited = true;

  useFetch({ url, isSingleRequest });

  return (
    <section data-testid="details" className="details no-touch">
      {isLoading && <div className="hourglass" />}
      {error && (
        <p data-testid="error" className="error-msg">
          Sorry, server hasn&apos;t answered. Try again later.{" "}
        </p>
      )}
      {isResponded && response.docs.length && <Card data={response.docs[0]} />}
    </section>
  );
};

export default Details;
