import React from "react";
import { v4 as uuidv4 } from "uuid";

import { IApiCharacter } from "../../../types/fetchData";

import Card from "./Card";

const CardsContainer: React.FC<{ results: IApiCharacter[] }> = props => {
  const { results } = props;

  return (
    <div data-testid="cardsContainer" className="cards">
      {results.map(result => {
        return <Card data={result} key={uuidv4()} />;
      })}
    </div>
  );
};

export default CardsContainer;
