import React from "react";
import { BrowserRouter } from "react-router-dom";

import Header from "./shared/Header/Header";
import Routes from "./Routes";

const App: React.FC = () => {
  return (
    <section data-testid="app">
      <BrowserRouter>
        <Header />
        <div className="working-zone">
          <Routes />
        </div>
      </BrowserRouter>
    </section>
  );
};

export default App;
